import React, { useState, useEffect } from 'react'
import {
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableCaption,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
  CFormSelect,
  CButton,
  CForm,
  FormEvent,
} from '@coreui/react'
import { DocsCallout, DocsExample } from 'src/components'
import axios from 'axios'

function Syscheck1() {
  const [posts, setPosts] = useState('default')
  const [state, setState] = useState('test')
  const result = { result: 'dd' }
  const api = axios.create({
    baseURL: 'http://localhost:8080',
  })

  const handleChange = (e) => {
    setState(e.target.value)
  }

  const handleSubmit = (e) => {
    SubmitTest(state)
    e.preventDefault()
  }

  function SubmitTest(props) {
    api
      .post('/api/check', null, {
        params: {
          userId: props,
        },
      })
      .then((response) => {
        result.result = response.data.test
        setPosts(response.data.test)
      })
      .catch((error) => {
        alert('There was an error!', error)
      })
  }

  return (
    <CRow>
      <CCol xs={12}>
        <CCard className="mb-4">
          <CCardHeader>
            <strong>System Check</strong> <small>was check</small>
          </CCardHeader>
          <CCardBody>
            <DocsExample>
              <CForm className="row gy-2 gx-3 align-items-center" onSubmit={handleSubmit}>
                <CCol xs="auto">
                  <CFormSelect id="autoSizingSelect" value={state.value} onChange={handleChange}>
                    <option selected>Test</option>
                    <option>Prod</option>
                  </CFormSelect>
                </CCol>
                <CCol xs="auto">
                  <CButton type="submit">Submit</CButton>
                </CCol>
              </CForm>
              <CCol xs="auto"></CCol>
              <CTable>
                <CTableHead>
                  <CTableRow>
                    <CTableHeaderCell scope="col">{posts}</CTableHeaderCell>
                    <CTableHeaderCell scope="col">{state}</CTableHeaderCell>
                    <CTableHeaderCell scope="col">{result.result}</CTableHeaderCell>
                    <CTableHeaderCell scope="col">ss</CTableHeaderCell>
                  </CTableRow>
                </CTableHead>
                <CTableBody>
                  <CTableRow>
                    <CTableHeaderCell scope="row">1</CTableHeaderCell>
                    <CTableDataCell>Mark</CTableDataCell>
                    <CTableDataCell>Otto</CTableDataCell>
                    <CTableDataCell>@mdo</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableHeaderCell scope="row">2</CTableHeaderCell>
                    <CTableDataCell>Jacob</CTableDataCell>
                    <CTableDataCell>Thornton</CTableDataCell>
                    <CTableDataCell>@fat</CTableDataCell>
                  </CTableRow>
                  <CTableRow>
                    <CTableHeaderCell scope="row">3</CTableHeaderCell>
                    <CTableDataCell colSpan="2">Larry the Bird</CTableDataCell>
                    <CTableDataCell>@twitter</CTableDataCell>
                  </CTableRow>
                </CTableBody>
              </CTable>
            </DocsExample>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  )
}
export default Syscheck1
